@import url(https://fonts.googleapis.com/css2?family=Oxanium:wght@800&family=Share+Tech+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{background-color:#d9d6d0;font-family:"Share Tech Mono", monospace}button{border:none;font-family:"Oxanium", monospace;text-transform:uppercase;cursor:pointer}ul,li,ol{list-style:none;display:flex}.cronometer{width:100vw;height:calc(100vh - 110px);max-width:300px;max-height:100%;min-height:450px;text-transform:uppercase;display:flex;flex-flow:column nowrap;justify-content:center;align-items:center;margin:0 auto}.cronometer__watcher{color:#d9d6d0;background-color:#0d0d0d;border:0 solid #bfbab0;border-top-width:5px;border-bottom-width:5px;order:2;margin:0;z-index:1;width:100%;min-width:250px}.cronometer__controllers{width:100%}.pointers{grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px;grid-row-gap:30px;row-gap:30px;display:flex;flex-flow:row wrap-reverse;justify-content:center;padding:30px 0;font-size:4rem}.pointers:nth-child(even){border:1px solid red}.pointers:nth-child(odd){text-align:right}.pointers__pointer{flex-basis:40%;padding:0 10px;background-color:#595958;position:relative}.pointers__pointer::before{position:absolute;font-size:1rem;top:-12px;left:105%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#595958}.pointers__pointer:nth-child(5)::before{content:"seconds"}.pointers__pointer:nth-child(3)::before{content:"minutes"}.pointers__pointer:nth-child(1)::before{content:"hours"}.pointers__pointer:nth-child(odd){text-align:right}.pointers__pointer:nth-child(even){text-align:left}.controllers{justify-content:space-around;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px}.controllers__list{flex-basis:25%;min-width:70px}.controllers__button{width:100%;height:40px;margin-inline:auto;font-size:1.5rem;background-color:#0d0d0d;color:#d9d6d0;border-top:5px solid #595958;transition:200ms}.controllers__button:active{-webkit-transform:translate(0, 15px);transform:translate(0, 15px)}.controllers__button:disabled{-webkit-transform:translate(0, 15px);transform:translate(0, 15px);border-top-color:red}@-webkit-keyframes slide_left_fade_in{0%{opacity:0;-webkit-transform:translateX(40px);transform:translateX(40px)}100%{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes slide_left_fade_in{0%{opacity:0;-webkit-transform:translateX(40px);transform:translateX(40px)}100%{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@-webkit-keyframes slide_bottom{0%{-webkit-transform:translateY(-50px);transform:translateY(-50px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes slide_bottom{0%{-webkit-transform:translateY(-50px);transform:translateY(-50px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}@-webkit-keyframes before_slide_bottom{0%{-webkit-transform:translate(-50%, -55%);transform:translate(-50%, -55%)}100%{-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}@keyframes before_slide_bottom{0%{-webkit-transform:translate(-50%, -55%);transform:translate(-50%, -55%)}100%{-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}@-webkit-keyframes disabled_slide_right{0%{-webkit-transform:translateX(0px);transform:translateX(0px)}100%{-webkit-transform:translateX(300px);transform:translateX(300px)}}@keyframes disabled_slide_right{0%{-webkit-transform:translateX(0px);transform:translateX(0px)}100%{-webkit-transform:translateX(300px);transform:translateX(300px)}}@-webkit-keyframes slide_top{0%{opacity:0;-webkit-transform:translateY(500px);transform:translateY(500px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px);opacity:1}}@keyframes slide_top{0%{opacity:0;-webkit-transform:translateY(500px);transform:translateY(500px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px);opacity:1}}.history{opacity:0;-webkit-animation-name:slide_top;animation-name:slide_top;-webkit-animation-duration:200ms;animation-duration:200ms;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;background-color:#0d0d0d;border:4px solid #0d0d0d;max-height:100%;width:calc(100vw - 100px);min-width:300px;max-width:60vw;margin:0 auto;overflow-y:scroll}@media screen and (orientation: landscape) and (min-width: 800px){.history{-webkit-animation-name:slide_left_fade_in, disabled_slide_right;animation-name:slide_left_fade_in, disabled_slide_right;-webkit-animation-duration:200ms, 1s;animation-duration:200ms, 1s;-webkit-animation-fill-mode:forwards, forwards;animation-fill-mode:forwards, forwards;height:100vh}}@media screen and (orientation: landscape) and (min-width: 800px){.history{border:none;border-left:4px solid #595958;max-width:none;max-width:initial;min-width:0;min-width:initial;width:auto;width:initial;flex-flow:column;position:fixed;right:0;top:0}}.history__clean{width:100%;padding:5px;transition:300ms}.history__clean:active{-webkit-transform:translatey(-5px);transform:translatey(-5px);-webkit-filter:brightness(50%);filter:brightness(50%)}.history__clean:hover{background-color:red}.history__list{display:grid;grid-template-columns:minmax(200px, 90%);justify-content:center;grid-column-gap:30px;-webkit-column-gap:30px;column-gap:30px}@media screen and (orientation: landscape) and (min-width: 800px){.history__list{grid-template-columns:200px;padding:0px 20px}}.history__item{-webkit-animation:slide_bottom 200ms forwards;animation:slide_bottom 200ms forwards;display:block;background-color:#595958;color:#d9d6d0;margin:15px 0;padding:10px;border-radius:5px;position:relative}@media screen and (orientation: landscape) and (min-width: 800px){.history__item::before{-webkit-animation:before_slide_bottom 200ms forwards;animation:before_slide_bottom 200ms forwards;content:"";position:absolute;width:40px;height:2px;background-color:#bfbab0;bottom:-17px;left:50%}.history__item:last-child::before{display:none}}.history__datetime{display:flex;flex-flow:row nowrap;font-family:"Courier Prime", monospace;justify-content:center;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px;opacity:0.5}.history__date{font-size:1rem}.history__time{font-size:0.8rem;font-family:"Courier Prime", monospace}.history__elapsed{margin-top:5px;font-size:2.4rem;font-weight:bold;text-align:center}.footer{padding:25px 0 10px;display:flex;flex-flow:column nowrap;grid-row-gap:10px;row-gap:10px;text-transform:uppercase}@media screen and (orientation: landscape) and (min-width: 800px){.footer{flex-flow:column nowrap}}.footer__item{width:-webkit-max-content;width:max-content;margin:0 auto}@media screen and (orientation: landscape) and (min-width: 800px){.footer__item{width:-webkit-max-content;width:max-content;margin:0 10px}}.footer__link{color:#0d0d0d;text-decoration:none}.footer__link:visited{color:#595958}

